<template>
  <div id="app">
    <img id='main-logo-mobile' class="navbar-item" rel='' :src="logoSource">
    <h1 class="main-title">School System</h1>
    <app-nav-bar></app-nav-bar>
    <div class="section main-module">
      <div class="notification">
        <transition name='fade' mode='out-in'>
          <router-view></router-view>
        </transition>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Navbar from './components/layout/Navbar.vue';
import Footer from './components/layout/Footer.vue';

export default {
  components: {
    AppNavBar: Navbar,
    AppFooter: Footer
  },
  name: 'app',
  data () {
    return {
      logoSource: require('./assets/logo.png'),
    }
  }
}


</script>

<style lang="scss">
$desktop: 650px;
@import '../node_modules/bulma/bulma.sass';

a {
  text-decoration: none !important;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  margin: 60px 20px;
  max-width: 800px;
  margin: 0 auto;
  padding: 60px 20px 0 20px;
}

h1, h2 {
  font-family: 'Lora', serif;
  font-weight: bolder;
  font-size: 32px;
  text-align: left;
}
/*
ul {
  list-style-type: none;
  padding: 0;
}*/

/*li {
  display: inline-block;
  margin: 0 10px;
}*/

a {
  color: #42b983;
}

input[type="checkbox"] {
  vertical-align: middle;
}

.main-module {
  max-width: unset;
  width: unset;
}

.button {
  margin: 10px 0;
}

table thead,  table tfoot {
  background-color: #d8ecff;
}

.custom-edit-button{
  margin: 0;
  height: 1.8em;
  padding: 0 6px;
  font-size: 14px;
  margin-top: 7px;
}
.table-name-row {
  max-width: 350px;
  word-break: break-all;
}

svg {
  height: 19px;
  width: 33px;
}

/* MOBILE SUPPORT */

.button.navbar-burger {
  margin: 0 auto;
}

#main-logo-mobile {
  display: none;
  height: 100px;
  width: auto;
  margin: 0 auto;
}

@media screen and (max-width: 750px) {

  h2, h4 {
    text-align: center;
  }
  table {
    margin: 0 auto;
  }
  .control {
    text-align: center;
  }
  .label {
    text-align: center;
  }

  .section.main-module {
    padding: 0px;
  }
  .notification {
    padding: 0;
  }
  #app {
    padding: 60px 0px 0 6px;
  }
  .main-title {
    text-align: center;
  }
  #main-logo-mobile {
    display: block;
  }
  .add-button {
    display: block;
  }
  #main-logo{
    display: none;
  }
}

@media screen and (max-width: 450px) {
  

  .table-restricted-size {
    max-width: 93px;
    word-break: break-all;
  }
}

/* Transition animations */
  .fade-enter {
    opacity: 0;
  }
  .fade-enter-active {
    transition: opacity 0.2s;
  }
  .fade-leave {
    opacity: 1;
  }
  .fade-leave-active {
    transition: opacity 0.2s;
    opacity: 0;
  }

  .slide-enter {
    opacity: 0;
  }
  .slide-enter-active {
    animation: slide-in 0.3s cubic-bezier(.68,-0.55,.24,1.97) forwards;
    transition: opacity 0.3s;
  }
  .slide-leave {
    opacity: 1;
  }
  .slide-leave-active {
    animation: slide-out 0.3s cubic-bezier(.68,-0.55,.24,1.97) forwards;
    transition: opacity 0.3s;
    opacity: 0;
  }

  @keyframes slide-in {
    0% {
      transform: translateX(40px);
    }
    100% {
      transform: translateX(0px);
    }
  }
  @keyframes slide-out {
    0% {
      transform: translateX(0px);
    }
    100% {
      transform: translateX(40px);
    }
  }
</style>



